
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#console {
  background-color: #151515;
  color: white;
  /*font-weight: bold;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
  padding: 10px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-family: monospace;
  white-space: normal;
  font-size: 1.2em;
}
info {
  color: #4CAF50;
  font-weight: bold;
  display: contents;
}
comment {
  color: #FFEB3B;
  font-weight: bold;
  display: contents;
  padding: 2px;
}
error {
  color: red;
  display: contents;
  padding: 2px;
  font-weight: bold;
}
critical {
  background-color: red;
  font-weight: bold;
  padding: 2px;
  /*display: contents;*/
}

